import Vue from 'vue'
import ElementUI from 'element-ui';
import {
	Loading,
	Message
} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Vant from 'vant';
import 'vant/lib/index.css';
import App from './App.vue'
import './assets/icon/iconfont.css';
/**/
import '@/assets/css/oneui.css'
import '@/assets/css/style.css'
import router from './router'
import store from './store/index.js'
import VueI18n from 'vue-i18n'

var axios = require('axios')
axios.defaults.baseURL = 'http://47.107.131.246:8443/api'
// 
axios.defaults.withCredentials = true

Vue.prototype.$axios = axios
Vue.use(ElementUI)
Vue.use(Vant);
Vue.config.productionTip = false

	// vue - PC
	if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
	    Vue.prototype.$pc = false
		// router.replace("/index_m")
	} else {
	    Vue.prototype.$pc = true
	    let winWidth = document.documentElement.offsetWidth ||
	    document.body.offsetWidth
	    winWidth = winWidth < 1366 ? 1366 : winWidth
	    let oHtml = document.getElementsByTagName('html')[0]
	    oHtml.style.fontSize = 100 * winWidth / 1920 + 'px'
	    window.addEventListener('resize', function () {
	        let winWidth = document.documentElement.offsetWidth || document.body.offsetWidth
	        winWidth = winWidth < 1366 ? 1366 : winWidth
	        let oHtml = document.getElementsByTagName('html')[0]
	        oHtml.style.fontSize = 100 * winWidth / 1920 + 'px'
	    })
	}

//,()
router.beforeEach((to, from, next) => {

	if (to.meta.requireAuth && to.path.startsWith('/admin')) {
		if (store.state.user.username||store.state.sessionUser.username) {
			axios.get('/authentication').then(resp => {
				if (resp) next()
			})
		} else {
			next({
				path: '/admin/login',
				query: {
					redirect: to.fullPath
				}
			})
		}
	} else if (to.meta.requireAuth) {
		if (store.state.user.username||store.state.sessionUser.username) {
			axios.get('/authentication').then(resp => {
				if (resp) next()
			})
		} else {
			next({
				path: '/login',
				query: {
					redirect: to.fullPath
				}
			})
		}
	} else {
		next()
	}
})

var loadinginstace;
// http request token
axios.interceptors.request.use(
	config => {
		loadinginstace = Loading.service({
			fullscreen: true
		})
		//  token
		if (store.state.user.token) {
			// tokenhttp headertoken
			// config.headers.Token = `token ${JSON.stringify(store.state.user.token)}`
			config.headers.Token = store.state.user.token
		}else if(store.state.sessionUser.token){
			config.headers.Token = store.state.sessionUser.token
		}
		return config;
	},
	err => {
		loadinginstace.close()
		Message.error({
			message: ''
		})
		return Promise.reject(err)
	}
)

// http response 
axios.interceptors.response.use(
	response => {
		loadinginstace.close()
		return response
	},
	error => {
		if (error.response) {
			var msg = error.response.data.message
			console.log(error.response.status)
			switch (error.response.status) {
				case 500:
					msg = JSON.parse(msg);
					if (msg.code == 401) {
						Message.error({
							message: msg.message
						})
						//  401 token
						router.replace({
							path: 'login',
							query: {
								redirect: router.currentRoute.fullPath
							}
						})
					}
					
			}
			loadinginstace.close()
		}
		loadinginstace.close()
		return Promise.reject(error.response.data) // 
	})


Vue.use(VueI18n) // 

const i18n = new VueI18n({
	locale: 'zh-CN', // 
	//this.$i18n.locale // locale
	messages: {
		'zh-CN': require('@/common/lang/zh'), // 
		'en-US': require('@/common/lang/en') // 
	}
})

new Vue({
	render: h => h(App),
	router,
	store,
	i18n
}).$mount('#app')
