<template>
	<div>
		<el-row :gutter="20">
			<el-col :span="12">
				<el-form :model="news" :rules="rules" ref="news" label-width="100px" class="demo-ruleForm">
					<el-form-item label="" prop="title">
						<el-input v-model="news.title"></el-input>
					</el-form-item>
					<el-form-item label="" prop="content">
						<DateTimePicker ref="time"></DateTimePicker>
					</el-form-item>
					<el-form-item label="" prop="notifyFight">
						<el-cascader placeholder="" v-model="news.notifyFight" :options="options" :props="{ multiple: true }"
						 filterable></el-cascader>
					</el-form-item>
					<el-form-item label="" prop="type">
						 <el-radio v-model="news.type" label="1"></el-radio>
						  <el-radio v-model="news.type" label="2"></el-radio>
					</el-form-item>
					<el-form-item label="" prop="title">
						<el-input v-model="news.title"></el-input>
					</el-form-item>
					<el-form-item label="" prop="title">
						<el-input v-model="news.title"></el-input>
					</el-form-item>
					<el-form-item label="" prop="title">
						<el-input v-model="news.startAirport"></el-input>
					</el-form-item>
					<el-form-item label="" prop="title">
						<el-input v-model="news.title"></el-input>
					</el-form-item>
					<el-form-item label="" prop="isNotify">
						<el-switch v-model="news.isNotify" active-text="" inactive-text="">
						</el-switch>
					</el-form-item>
					<el-form-item label="" prop="notifyContent">
						<el-input type="textarea" :rows="7" v-model="news.notifyContent"></el-input>
					</el-form-item>
					<el-form-item label="" prop="status">
						<el-switch v-model="news.status" active-color="#13ce66" inactive-color="#ff4949" active-text="" inactive-text="">
						</el-switch>
					</el-form-item>
					<el-form-item>
						<el-button-group>
						<el-button @click="fileUpload">
							<i class="el-icon-folder-add"></i>
						</el-button>
						<el-button type="primary" @click="submitForm('news')"> </el-button>
						<el-button @click="resetForm('news')"> </el-button>
						</el-button-group>
					</el-form-item>
				</el-form>
			</el-col>
		</el-row>
		<el-dialog title="" :visible.sync="fileForm">
			<el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-change="handleChange">
				<el-button type="primary"></el-button>
				<div slot="tip" class="el-upload__tip">jpg/png500kb</div>
			</el-upload>
			<div slot="footer" class="dialog-footer">
				<el-button @click="fileForm = false"> </el-button>
			</div>
		</el-dialog>
	</div>

</template>

<script>
	import DateTimePicker from '@/components/home/utils/DateTimePicker.vue'
	export default {
		name: 'NewsAdd',
		components: {
			DateTimePicker
		},
		created() {
			this.$axios
				.get('/admin/chinaFight/all')
				.then(resp => {
					var chinaFights = this.filterOptions(resp.data.data)
					this.options = [{
						label: '',
						children: chinaFights
					}]
				});
			this.$axios
				.get('/admin/abroadFight/all')
				.then(resp => {
					var abroadFights = this.filterOptions(resp.data.data)
					this.options.push({
						label: '',
						children: abroadFights
					});
				});
		},
		data() {
			return {
				imageUrl: '',
				news: {
					title: '',
					content: '',
					notifyFight: '',
					notifyContent: '',
					status: true,
					isNotify: false,
					type:'1'
				},
				fileForm: false,
				fileList: '',
				rules: {
					title: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
				},
				options: [{
					value: 'zhinan',
					label: '12-25',
					children: [{
						value: 'shejiyuanze',
						label: '',
						children: [{
							value: 'yizhi',
							label: '',
							children: [{
									value: 'id',
									label: '',
									children: [{
										value: 'id',
										label: '',
									}]
								},
								{
									value: 'id',
									label: '',
									children: [{
										value: 'id',
										label: '',
									}]
								}
							]
						}, {
							value: 'fankui',
							label: ''
						}, {
							value: 'xiaolv',
							label: ''
						}, {
							value: 'kekong',
							label: ''
						}]
					}, {
						value: 'daohang',
						label: '',
						children: [{
							value: 'cexiangdaohang',
							label: ''
						}, {
							value: 'dingbudaohang',
							label: ''
						}]
					}]
				}, {
					value: 'zujian',
					label: '',
					children: [{
						value: 'basic',
						label: 'Basic',
						children: [{
							value: 'layout',
							label: 'Layout '
						}, {
							value: 'color',
							label: 'Color '
						}, {
							value: 'typography',
							label: 'Typography '
						}, {
							value: 'icon',
							label: 'Icon '
						}, {
							value: 'button',
							label: 'Button '
						}]
					}, {
						value: 'form',
						label: 'Form',
						children: [{
							value: 'radio',
							label: 'Radio '
						}, {
							value: 'checkbox',
							label: 'Checkbox '
						}, {
							value: 'input',
							label: 'Input '
						}, {
							value: 'input-number',
							label: 'InputNumber '
						}, {
							value: 'select',
							label: 'Select '
						}, {
							value: 'cascader',
							label: 'Cascader '
						}, {
							value: 'switch',
							label: 'Switch '
						}, {
							value: 'slider',
							label: 'Slider '
						}, {
							value: 'time-picker',
							label: 'TimePicker '
						}, {
							value: 'date-picker',
							label: 'DatePicker '
						}, {
							value: 'datetime-picker',
							label: 'DateTimePicker '
						}, {
							value: 'upload',
							label: 'Upload '
						}, {
							value: 'rate',
							label: 'Rate '
						}, {
							value: 'form',
							label: 'Form '
						}]
					}, {
						value: 'data',
						label: 'Data',
						children: [{
							value: 'table',
							label: 'Table '
						}, {
							value: 'tag',
							label: 'Tag '
						}, {
							value: 'progress',
							label: 'Progress '
						}, {
							value: 'tree',
							label: 'Tree '
						}, {
							value: 'pagination',
							label: 'Pagination '
						}, {
							value: 'badge',
							label: 'Badge '
						}]
					}, {
						value: 'notice',
						label: 'Notice',
						children: [{
							value: 'alert',
							label: 'Alert '
						}, {
							value: 'loading',
							label: 'Loading '
						}, {
							value: 'message',
							label: 'Message '
						}, {
							value: 'message-box',
							label: 'MessageBox '
						}, {
							value: 'notification',
							label: 'Notification '
						}]
					}, {
						value: 'navigation',
						label: 'Navigation',
						children: [{
							value: 'menu',
							label: 'NavMenu '
						}, {
							value: 'tabs',
							label: 'Tabs '
						}, {
							value: 'breadcrumb',
							label: 'Breadcrumb '
						}, {
							value: 'dropdown',
							label: 'Dropdown '
						}, {
							value: 'steps',
							label: 'Steps '
						}]
					}, {
						value: 'others',
						label: 'Others',
						children: [{
							value: 'dialog',
							label: 'Dialog '
						}, {
							value: 'tooltip',
							label: 'Tooltip '
						}, {
							value: 'popover',
							label: 'Popover '
						}, {
							value: 'card',
							label: 'Card '
						}, {
							value: 'carousel',
							label: 'Carousel '
						}, {
							value: 'collapse',
							label: 'Collapse '
						}]
					}]
				}]
			};
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						this.$axios
							.post('/admin/news/add', {
								title: this.news.title,
								planStartTime: this.$refs.time.time[0],
								planEndTime: this.$refs.time.time[1],
								notifyFight: this.news.notifyFight,
								notifyContent: this.news.notifyContent,
								status: this.news.status ? 0 : 1
							})
							.then(resp => {
								if (resp.data.code === 200) {
									var data = resp.data.msg;
									this.$message({
										message: data,
										type: 'success'
									});
									var path = this.$route.query.redirect
									this.$router.replace({
										path: path === '/admin/news/add' || path === undefined ? '/admin/news/show' : path
									})
								}
							})
							.catch(failResponse => {})
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			goBack() {
				console.log('go back');
			},
			fileUpload() {
				this.fileForm = true;
			},
			handleChange(file, fileList) {
				const isExcel = file.type === ('xls') || ('xlsx');
				this.fileList = fileList.slice(-3);
			},
			filterOptions(beforeData) {
				var tempArr = [];
				this.afterData = [];
				for (let i = 0; i < beforeData.length; i++) {
					if (tempArr.indexOf(beforeData[i].startCity) === -1) {
						this.afterData.push({
							label: beforeData[i].startCity,
							children: [beforeData[i]]
						});
						tempArr.push(beforeData[i].startCity);
					} else {
						for (let j = 0; j < this.afterData.length; j++) {
							if (this.afterData[j].label == beforeData[i].startCity) {
								this.afterData[j].children.push(beforeData[i]);
								break;
							}
						}
					}
				}
				for (let i = 0; i < this.afterData.length; i++) {
					var tempArr2 = [];
					this.afterDataChilds = [];
					this.afterDataChild = this.afterData[i].children;
					for (let m = 0; m < this.afterDataChild.length; m++) {
						if (tempArr2.indexOf(this.afterDataChild[m].endCity) === -1) {
							this.afterDataChilds.push({
								label: this.afterDataChild[m].endCity,
								children: [{
									label: this.afterDataChild[m].number,
									value: this.afterDataChild[m].id
								}]
							});
							tempArr2.push(this.afterDataChild[m].endCity);
						} else {
							for (let j = 0; j < this.afterDataChilds.length; j++) {
								if (this.afterDataChilds[j].label == this.afterDataChild[m].endCity) {
									this.afterDataChilds[j].children.push({
										label: this.afterDataChild[m].number,
										value: this.afterDataChild[m].id
									});
									break;
								}
							}
						}
					}
					this.afterData[i].children = this.afterDataChilds;
				}
				return this.afterData;
			}
		}
	}
</script>

<style scoped="scoped">
	.el-form {
		margin-top: 30px;
	}

	.el-upload__input {
		display: none !important;
	}

	.el-upload input[type="file"] {
		display: none !important;
	}
</style>
